.newTool {
  position: relative;
  height: 639px;
  padding: 55px 0;
  background: linear-gradient(0deg, #98c3e8 8.84%, rgba(152, 195, 232, 0) 31.12%) url("./img/illustration.svg");
}

.newTool__inner {
  width: auto;
  max-width: 530px;
}

.newTool__title {
  font-family: "Times New Roman", Times, serif;
  font-size: 56px;
  line-height: 74px;
  letter-spacing: 0.04em;
  color: #1f3f68;
  margin: 0 0 35px;
}

.newTool__img {
  display: block;
  position: absolute;
  top: -40px;
  right: 23px;
}

.newTool__descr {
  font-family: "Gilroy";
  font-weight: 400;
  font-size: 16px;
  line-height: 26px;
  width: auto;
  max-width: 425px;
  letter-spacing: 0.05em;
  color: #1f3f68;
  opacity: 0.6;
  margin-bottom: 77px;
}

@media only screen and (min-width: 1600px) {
  .newTool__img {
    right: 5%;
  }
}

@media only screen and (min-width: 2500px) {
  .newTool__img {
    right: 25%;
  }
}

@media only screen and (max-width: 1199px) {
  .newTool__img {
    top: 90px;
    height: 70%;
  }
}

@media only screen and (max-width: 991px) {
  .newTool__inner {
    max-width: 100%;
  }

  .newTool__img {
    top: 90px;
    height: 50%;
  }
}

@media only screen and (max-width: 576px) {
  .newTool__descr {
    max-width: 100%;
  }
}